window.addEventListener("scroll", function () {
  let shadowFix = document.querySelector(".shadowFix");
  document.querySelector(".shadowFix").setAttribute("filter", "url(#f6)");

  const parallax1 = document.querySelector(".circle-2");
  const parallax2 = document.querySelector(".circle-1");
  const parallax3 = document.querySelector(".circle-3");
  const parallax5 = document.querySelector(".circle-4");
  const parallax6 = document.querySelector(".circle-5");
  const parallax4 = document.querySelector(".hero__img");

  const parallax7 = document.querySelector(".circle-6");
  const parallax8 = document.querySelector(".circle-7");
  const parallax9 = document.querySelector(".circle-8");
  const parallax10 = document.querySelector(".circle-9");

  let offset = window.pageYOffset;
  if (parallax1 != null) {
    parallax1.style.transform = "translateY(" + offset * -0.1 + "px)";
    parallax4.style.transform = "translateY(" + offset * -0.1 + "px)";
  }

  parallax2.style.transform = "translateY(" + offset * -0.5 + "px)";
  parallax3.style.transform = "translateY(" + offset * -0.8 + "px)";

  parallax5.style.transform = "translateY(" + offset * -0.6 + "px)";
  parallax6.style.transform = "translateY(" + offset * -0.2 + "px)";

  ///
  parallax7.style.transform = "translateY(" + offset * -0.4 + "px)";
  parallax8.style.transform = "translateY(" + offset * -0.7 + "px)";
  parallax9.style.transform = "translateY(" + offset * -0.2 + "px)";
  parallax10.style.transform = "translateY(" + offset * -0.6 + "px)";
});

//////////////////////
//modal
let body = document.querySelector("body");

(function () {
  /* Opening modal window function */
  function openModal() {
    /* Get trigger element */
    var modalTrigger = document.querySelectorAll(".items>.item");

    /* Set onclick event handler for all trigger elements */
    for (var i = 0; i < modalTrigger.length; i++) {
      modalTrigger[i].onclick = function () {
        var target = this.getAttribute("href").substr(1);
        var modalWindow = document.getElementById(target);

        modalWindow.classList
          ? modalWindow.classList.add("open")
          : (modalWindow.className += " " + "open");

        if (modalWindow.classList.contains("open")) {
          body.classList.add("ofHidden");
        } else {
          body.classList.remove("ofHidden");
        }
      };
    }
  }

  /* Opening modal window function */
  function openModal2() {
    /* Get trigger element */
    var modalTrigger2 = document.querySelector(".cart");

    /* Set onclick event handler for all trigger elements */

    modalTrigger2.onclick = function () {
      var target = this.getAttribute("href").substr(1);
      var modalWindow = document.getElementById(target);

      modalWindow.classList
        ? modalWindow.classList.add("open")
        : (modalWindow.className += " " + "open");

      if (modalWindow.classList.contains("open")) {
        body.classList.add("ofHidden");
      } else {
        body.classList.remove("ofHidden");
      }
    };
  }

  function closeModal() {
    /* Get close button */
    var closeButton = document.getElementsByClassName("jsModalClose");
    var closeOverlay = document.getElementsByClassName("jsOverlay");

    /* Set onclick event handler for close buttons */
    for (var i = 0; i < closeButton.length; i++) {
      closeButton[i].onclick = function () {
        body.classList.remove("ofHidden");
        var modalWindow = this.parentNode.parentNode;

        modalWindow.classList
          ? modalWindow.classList.remove("open")
          : (modalWindow.className = modalWindow.className.replace(
              new RegExp(
                "(^|\\b)" + "open".split(" ").join("|") + "(\\b|$)",
                "gi"
              ),
              " "
            ));
      };
    }

    /* Set onclick event handler for modal overlay */
    for (var i = 0; i < closeOverlay.length; i++) {
      closeOverlay[i].onclick = function () {
        var modalWindow = this.parentNode;
        body.classList.remove("ofHidden");

        modalWindow.classList
          ? modalWindow.classList.remove("open")
          : (modalWindow.className = modalWindow.className.replace(
              new RegExp(
                "(^|\\b)" + "open".split(" ").join("|") + "(\\b|$)",
                "gi"
              ),
              " "
            ));
      };
    }
  }

  /* Handling domready event IE9+ */
  function ready(fn) {
    if (document.readyState != "loading") {
      fn();
    } else {
      document.addEventListener("DOMContentLoaded", fn);
    }
  }

  /* Triggering modal window function after dom ready */
  ready(openModal);
  ready(openModal2);
  ready(closeModal);
})();
